.resistor-svg {
  width: 100%;
  text-align: center;
  margin-top: 30px; }
  .resistor-svg .body {
    fill: #E9DDAF;
    stroke-width: 0.5;
    stroke-linecap: butt;
    stroke-linejoin: miter;
    stroke-opacity: 1; }
  .resistor-svg .clickable {
    opacity: 1;
    fill-opacity: 1;
    stroke: none;
    stroke-width: 0.94488192;
    stroke-opacity: 1;
    cursor: pointer; }
  .resistor-svg .line {
    color: #000000; }

.black {
  fill: #000000; }

.brown {
  fill: #964B00; }

.red {
  fill: #FF0000; }

.orange {
  fill: #FFA500; }

.yellow {
  fill: #FFFF00; }

.green {
  fill: #9ACD32; }

.blue {
  fill: #6495ED; }

.violet {
  fill: #9400D3; }

.gray {
  fill: #A0A0A0; }

.white {
  fill: #FFFFFF; }

.gold {
  fill: #CFB53B; }

.silver {
  fill: #C0C0C0; }

.pink {
  fill: #FF69B4; }

.transparent {
  fill: #E9DDAF;
  stroke: #000000 !important;
  stroke-dasharray: 5, 5; }



